@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

/* Reset and base styles  */
* {
    box-sizing: border-box;
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

*:focus, *:active {
    outline: none;
}

/* Links */
a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

:root {
    --gray-50: #F6F8FF;
    --gray-400: #1E2A47;
    --gray-500: #141D2F;

    --main-color: #0079FF;
    --error-color: #F74646;
  }

body {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    background-color: var(--gray-500);
}

.container {
    width: 100%;
    padding: 0 24px;
}

@media screen and (min-width: 767px) {
    .container {
        width: 734px;
        padding: 0;
        margin: 0 auto;
    }
}
